.button--large, .button--select {
  background-color: var(--color-cyan-6);
  border-radius: var(--button-radius);
  color: var(--color-white-5);
  transition-property: background-color;
}
.button--large:is(:hover, :focus-visible), .button--select:is(:hover, :focus-visible) {
  background-color: var(--color-cyan-8);
}

/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin, padding and set font inherit to all elements */
* {
  padding: 0;
  margin: 0;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

html,
body {
  height: 100%;
}

/* Set core body defaults */
body {
  text-rendering: optimizeSpeed;
  line-height: 1;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Remove default underline from A elements */
a,
a:visited,
a:active {
  text-decoration: none;
}

/* Remove list styles on ul, ol elements */
ul,
ol {
  list-style: none;
}

/* Make images easier to work with */
img,
picture,
svg {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Set core buttons and inputs defaults */
button,
[type=button],
[type=reset],
[type=submit] {
  border: 0;
  cursor: pointer;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }
  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
.design {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1000;
  opacity: 0.2;
  pointer-events: none;
}

:root {
  --radius-1: 0.5rem;
  --border-0: 0.063rem solid var(--color-gray-1);
  --border-1: 0.063rem solid var(--color-gray-2);
  --duration-2: 200ms;
}

body {
  background-color: var(--color-white-8);
}

span {
  display: block;
}

:root {
  --font-commissioner: "Commissioner", sans-serif;
  --font-size-1: 0.938rem;
  --font-size-2: 1.125rem;
  --font-size-3: 1.25rem;
  --font-size-4: 1.5rem;
  --font-size-5: 2rem;
  --font-size-6: 2.5rem;
  --font-weight-4: 400;
  --font-weight-5: 500;
  --font-weight-7: 700;
}
@media screen and (min-width: 46.875em) {
  :root {
    --font-size-1: 1rem;
    --font-size-2: 1.125rem;
    --font-size-3: 1.25rem;
    --font-size-4: 1.5rem;
    --font-size-5: 2rem;
    --font-size-6: 2.5rem;
  }
}

body {
  font-family: var(--font-commissioner);
  font-size: var(--font-size-1);
}

.heading, .heading--h3, .heading--h2, .heading--h1 {
  font-weight: var(--font-weight-7);
  color: var(--color-black);
}
.heading--h1 {
  max-width: 12rem;
  margin-block-end: 1.125rem;
  font-size: var(--font-size-3);
  line-height: 1.15;
}
.heading--h2 {
  margin-block-end: 1.75rem;
  font-size: var(--font-size-2);
}
.heading--h3 {
  font-size: var(--font-size-1);
}

.paragraph {
  color: var(--color-gray-5);
  line-height: 1.6;
  margin-block-end: 1.5rem;
}

:root {
  --color-cyan-6: hsl(176, 50%, 47%);
  --color-cyan-8: hsl(176, 72%, 28%);
  --color-black: hsl(0, 0%, 0%);
  --color-gray-1: hsl(0, 0%, 48%, 0.1);
  --color-gray-2: hsl(0, 0%, 48%, 0.3);
  --color-gray-5: hsl(0, 0%, 48%);
  --color-white-5: hsl(0, 0%, 100%);
  --color-white-8: hsl(0, 0%, 98%);
}

.container, .container--main, .container--header {
  --container-padding-inline: 1.5rem;
  display: flex;
  padding-inline: var(--container-padding-inline);
}
.container--header {
  --container-height: 18.75rem;
  --container-padding-block-start: 2rem;
  justify-content: center;
  height: var(--container-height);
  background: url("../images/image-hero-mobile.jpg") center/cover no-repeat;
  padding-block-start: var(--container-padding-block-start);
}
.container--main {
  --container-row-gap: 1.5rem;
  --container-top: -3.5rem;
  flex-direction: column;
  align-items: center;
  row-gap: var(--container-row-gap);
  position: relative;
  top: var(--container-top);
}

.button, .button--select, .button--bookmark, .button--large, .button--menu {
  --button-radius: calc(var(--button-height) / 2);
  --button-transition: var(--duration-2) ease;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: var(--font-weight-5);
  transition: var(--button-transition);
}
.button--menu {
  background-color: transparent;
}
.button--menu .icon, .button--menu .icon--close, .button--menu .icon--hamburger {
  --icon-width: 1.25rem;
  --icon-height: var(--icon-width);
  width: var(--icon-width);
  height: var(--icon-height);
}
.button--menu .icon--close {
  display: none;
}
.button--menu.active .icon--hamburger {
  display: none;
}
.button--menu.active .icon--close {
  display: block;
}
.button--large {
  --button-width: 13.375rem;
  --button-height: 3.5rem;
  width: min(100%, var(--button-width));
  height: var(--button-height);
  padding-inline: 0.5rem;
}
.button--bookmark {
  --button-width: 3.5rem;
  --button-height: var(--button-width);
  width: var(--button-width);
  height: var(--button-height);
  flex-shrink: 0;
  background-color: var(--color-white-8);
  border-radius: 50%;
  overflow: hidden;
  transition-property: color;
}
.button--bookmark circle {
  transition: fill var(--button-transition);
}
.button--bookmark:is(:hover, :focus-visible) circle {
  fill: var(--color-gray-5);
}
.button--bookmark .button__text {
  display: none;
}
.button--select {
  --button-width: 9.75rem;
  --button-height: 3rem;
  width: min(100%, var(--button-width));
  height: var(--button-height);
}

.section, .section--reward, .section--stats, .section--intro {
  --section-max-width: 45.75rem;
  --section-padding-inline: 1.375rem;
  --section-padding-block-end: 2.375rem;
  width: min(100%, var(--section-max-width));
  display: flex;
  flex-direction: column;
  position: relative;
  background-color: var(--color-white-5);
  border: var(--border-0);
  border-radius: var(--radius-1);
  padding-inline: var(--section-padding-inline);
  padding-block-end: var(--section-padding-block-end);
}
.section--intro {
  --section-padding-block-start: 3.25rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-block-start: var(--section-padding-block-start);
  text-align: center;
}
.section--intro .section__logo {
  width: max-content;
  position: absolute;
  top: -1.75rem;
}
.section--stats {
  --section-padding-block-start: 2.25rem;
  --section-row-gap: 0.5rem;
  align-items: center;
  row-gap: var(--section-row-gap);
  padding-block-start: var(--section-padding-block-start);
}
.section--stats .section__bar {
  --bar-height: 0.75rem;
  --bar-radius: calc(var(--bar-height) / 2);
  width: 100%;
  height: var(--bar-height);
  background-color: var(--color-white-8);
  border-radius: var(--bar-radius);
}
.section--stats .section__bar .bar__progress {
  --bar-progress-width: 89.914%;
  width: var(--bar-progress-width);
  height: var(--bar-height);
  background-color: var(--color-cyan-6);
  border-radius: var(--bar-radius);
}
.section--reward {
  --section-padding-block-start: 2.625rem;
  padding-block-start: var(--section-padding-block-start);
}

.wrapper, .wrapper--select, .wrapper--label, .wrapper--button {
  width: 100%;
  display: flex;
}
.wrapper--button {
  justify-content: space-between;
  column-gap: 0.5rem;
}
.wrapper--label {
  flex-direction: column;
  row-gap: 0.625rem;
  margin-block-end: 1.5rem;
}
.wrapper--select {
  flex-direction: column;
  row-gap: 1.625rem;
  padding-block-start: 0.25rem;
}

.stats, .stats--day, .stats--column {
  display: flex;
}
.stats--column {
  --stats-row-gap: 0.875rem;
  flex-direction: column;
  align-items: center;
  row-gap: var(--stats-row-gap);
  padding-block-end: 1.5rem;
}
.section--stats .stats--column:nth-of-type(n + 2) {
  position: relative;
  padding-block-start: 1.875rem;
}
.section--stats .stats--column:nth-of-type(n + 2)::before {
  content: "";
  display: block;
  position: absolute;
  width: 5rem;
  border-block-start: var(--border-1);
  top: 0;
}
.stats--day {
  align-items: center;
  column-gap: 0.625rem;
}
.stats__number {
  font-size: var(--font-size-5);
  font-weight: var(--font-weight-7);
}
.stats__text {
  font-weight: var(--font-weight-5);
  color: var(--color-gray-5);
}

.reward {
  margin-block-start: 0.625rem;
}
.reward__list {
  display: grid;
  row-gap: 1.5rem;
}
.reward__item.disabled {
  opacity: 0.5;
}
.reward__item.disabled .button--select {
  background-color: var(--color-gray-5);
  cursor: not-allowed;
}
.reward__wrapper {
  --wrapper-padding: 1.5rem;
  border: var(--border-1);
  border-radius: var(--radius-1);
  padding: var(--wrapper-padding);
}
.reward__values {
  font-weight: var(--font-weight-5);
  color: var(--color-cyan-6);
}

.nav {
  --nav-max-width: 69.25rem;
  width: min(100%, var(--nav-max-width));
  height: fit-content;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
}
.nav__menu {
  --menu-max-width: 30rem;
  width: min(100%, var(--menu-max-width));
  position: absolute;
  top: calc(100% + 2.25rem);
  right: 0;
  visibility: hidden;
  background-color: var(--color-white-5);
  border-radius: var(--radius-1);
}
.nav__menu .menu__list {
  display: flex;
  flex-direction: column;
}
.nav__menu .menu__item:nth-of-type(n + 2) {
  border-block-start: var(--border-1);
}
.nav__menu .menu__link {
  --link-height: 4.375rem;
  --link-padding-inline-start: 1.5rem;
  height: var(--link-height);
  display: flex;
  align-items: center;
  padding-inline-start: var(--link-padding-inline-start);
  font-size: var(--font-size-2);
  font-weight: var(--font-weight-5);
  color: var(--color-black);
}